@import url(https://fonts.googleapis.com/css2?family=Inter&display=swap);
body {
  background-color: #fafafa !important;
  margin: auto;
  padding: 0px;
  overflow-x: hidden;
  display: grid;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header__container {
width: 100%;
position: fixed;
background-color: #fff;
padding-right:15px;
padding-left:15px;
margin-right:auto;
margin-left:auto;
z-index: 100;
}
img {
  max-width: 100%;
  height: auto;
}

.sec__one {
    position: relative;
    width: 100%;
    display: flex;
    padding: 60px 0;
}
.sec__one h1{
  text-align: center;
  display: block;
  margin: 0 auto;
  color: #07a8ff;
}
.navBar .logo img {
  float: left;
  max-height: 120px;
}
.is-active {
  color: #34aadc!important;
}

.navBar .col-md-3 {
  z-index: 7;
}
ul.main-menu {
  display: inline-flex;
  list-style: none;
  margin: 0px auto 0;
  padding: 0;
  float: right;
  z-index: 7;
}




ul.main-menu>li.menu-item:first-child {
  padding-left: 0;
  margin-left: 0;
}
.menu-item {
  line-height: 23px;
}
li {
  list-style-type: none;
}
.menu-item a {
  font-size: 1.1rem;
  font-weight: 600;
  position: relative;
  display: block;
  color: #545050;
  margin: 27px 30px;
  text-decoration: none;
  cursor: pointer;
}
.header__middle__logo{
  width: 20%;
  margin-left: 10%;
  display: inline-block;
  float: left;
}

.menu-item.menu__qqqitem a{
  margin: 33px 10px;
}
.header__middle__search {
  width: 7%;
  display: inline-block;
  float: right;
  padding: 36px 3px 27px 3px;
}
.header__middle {
  display: flex;
  width: 100%;
  float: left;
  position: relative;
}
.header__middle__logo > a > h1 {
  color: #34aadc;
  text-transform: uppercase;
  text-decoration: none;
  margin-top: 33px;
  
}

.sub__menus__arrows{
  position: relative;
}
.sub__menus__arrows svg, .sub__menus__arrows__full svg{
  padding-top: 0px;
  position: absolute;
  top: 13px;
  
}

.sub__menus {
  position: absolute;
  display: none;
  background: white;
  /* border: 1px solid lavender; */
  width: 227px;
  left: -17px;
  padding: 2px 0 0 0;
  z-index: 1000;
  /* box-shadow: 0 10px 12px rgb(22 33 39 / 25%); */
}

@media (max-width:767px) { 
  .header__middle .active {color: #34aadc!important;}
}

@media (min-width:992px) { 
  .sub__menus__arrows:hover .sub__menus{
    display: block;
  }
  .sub__menus__arrows__full:hover .sub__menus{
    display: block;
    z-index: 1000000;
  }
}
.menu-item .sub__menus  a {
  padding: 7px 0 0px 0;
  margin: 7px 27px;
}
.menu-item:hover > a{
  color: #07a8ff !important;
}
.menu-item .sub__menus li:hover a{
  color: #07a8ff !important;
}

.header__middle__logo img{
  max-width: 25%;
  margin: 0px;
}


@media(max-width:991px){
.header__middle__logo {width: 77%;}
.header__middle__logo img {max-width: 10;margin: 27px 0 0 0;}
.main-nav .menubar__button:hover {background-color: #2e2e2e;}
.main-nav .menu-item:hover > a {color: #07a8ff !important}
.main-nav .menubar__button {
  display: block!important;
  float: right;
  background-color: #2e2e2e;
  color: #fff;
  padding: 6px 7px;
  border-radius: 5px;
  margin: 27px auto 0;
  cursor: pointer;
  position: relative;
  z-index: 10037;
}
.main-nav .menubar__button svg{font-size: 27px;}
.main-nav ul.menuq2 {
